.header {
  display: flex;
  width: 100%;
  justify-content: flex-start;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  padding-bottom: 41px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 540px) {
  .header {
    padding-bottom: 30px;
  }
}
